$scale: 16;
// SHADOW
.button-small {
    font-style: normal;
    font-weight: 500;
    font-size: calc(12/$scale)+rem;
    line-height: calc(12/$scale)+rem;
}

.button-medium {
    font-style: normal;
    font-weight: 500;
    font-size: calc(14/$scale)+rem;
    line-height: calc(14/$scale)+rem;
}

.button-large {
    font-style: normal;
    font-weight: 500;
    font-size: calc(16/$scale)+rem;
    line-height: calc(18/$scale)+rem; // O valor de 16.33 deve ser respeitado para os botões não ficarem com altura diferente a do Figma
}

// BODY
.body-01 {
    font-style: normal;
    font-weight: 400;
    font-size: calc(12/$scale)+rem;
    line-height: calc(18/$scale)+rem;
}

.body-02 {
    font-style: normal;
    font-weight: 400;
    font-size: calc(14/$scale)+rem;
    line-height: calc(21/$scale)+rem;
}

.body-03 {
    font-style: normal;
    font-weight: 400;
    font-size: calc(16/$scale)+rem;
    line-height: calc(24/$scale)+rem;
}

//SPORTING STYLES
.code-01 {
    font-family: 'Roboto Mono';
    font-style: normal;
    font-weight: 400;
    font-size: calc(12/$scale)+rem;
    line-height: calc(18/$scale)+rem;
}

.code-02 {
    font-family: 'Roboto Mono';
    font-style: normal;
    font-weight: 400;
    font-size: calc(14/$scale)+rem;
    line-height: calc(21/$scale)+rem;
}

.helper-text-01 {
    font-style: italic;
    font-weight: 400;
    font-size: calc(12/$scale)+rem;
    line-height: calc(18/$scale)+rem;
}

.label-01 {
    font-style: normal;
    font-weight: 500;
    font-size: calc(12/$scale)+rem;
    line-height: calc(18/$scale)+rem;
}

